
import { defineComponent } from "vue"
export default defineComponent({
    name: 'Yannick',
    data(){
        return {
            total: 0,
            yannick : 'Yannick',
            tabOfList : [
                'manger',
                'boire',
                'dormir'
            ]
        }
    },
    methods:{
        increment() : void{
            this.total += 1;
        },
        pushToList(){
            this.tabOfList.push(this.yannick);
        }
    }
})
