import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/Connexion',
    name: 'connexion',
    component: () => import(/* webpackChunkName: "about" */ '../views/Connexion.vue')
  },
  {
    path: '/ConnexionCheck',
    name: 'ConnexionCheck',
    component: () => import(/* webpackChunkName: "about" */ '../views/CheckScreen/ConnexionCheck.vue')
  },
  {
    path: '/Dashboard',
    name: 'Dashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/TestDashboard.vue')
  },
  {
    path: '/ProfileEdit',
    name: 'ProfileEdit',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/ProfileEdit.vue')
  },
  {
    path: '/EditDashboard',
    name: 'EditDashboard',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/EditDashboard.vue')
  },
  {
    path: '/Home',
    name: 'Homescreen',
    component: () => import(/* webpackChunkName: "about" */ '../views/Dashboard/HomeScreen.vue')
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
